"use client";
import axios from "axios";
import { useEffect, useState } from "react";
import { Input } from "@/components/ui/input";

interface Weather {
  city: string;
  current: {
    temperature: number;
    feels_like: number;
    humidity: number;
    pressure: number;
    wind_speed: number;
    visibility: number;
    description: string;
    icon: string;
  };
}

export default function Dashboard() {
  const [city, setCity] = useState("Boac");
  const [weather, setWeather] = useState<Weather | null>(null);

  const getWeather = async (cityName: string) => {
    try {
      const response = await axios.get(
        `http://localhost:5000/get-weather?city=${cityName}`
      );
      setWeather(response.data);
    } catch (error) {
      console.error("Error fetching weather data:", error);
      setWeather(null);
    }
  };

  useEffect(() => {
    getWeather(city);
  }, [city]);

  // Function to get background image path dynamically
  const getBackgroundImage = (description: string | undefined) => {
    if (!description) return "/images/default.jpg"; // Default background
    const lowerDesc = description.toLowerCase();
    if (lowerDesc.includes("rain")) return "/a";
    if (lowerDesc.includes("cloud")) return "/images/cloudy.jpg";
    if (lowerDesc.includes("clear")) return "/images/clear.jpg";
    if (lowerDesc.includes("snow")) return "/images/snowy.jpg";
    return "/images/default.jpg";
  };

  return (
    <div
      className="min-h-screen flex items-center justify-center bg-cover bg-center transition-all duration-500"
      style={{
        backgroundImage: `url(${getBackgroundImage(weather?.current.description)})`,
      }}
    >
      <div className="w-80 h-96 bg-white bg-opacity-80 rounded-md p-4 shadow-lg">
        <div className="flex flex-col items-center">
          <Input
            value={city}
            onChange={(e) => setCity(e.target.value)}
            placeholder="Enter city name"
            className="mb-4"
          />
          {weather ? (
            <>
              <img src={weather.current.icon} alt="Weather icon" className="w-16 h-16" />
              <h2 className="text-xl font-bold">{weather.city}</h2>
              <p className="text-lg">{weather.current.temperature}Â°C</p>
              <p>Humidity: {weather.current.humidity}%</p>
              <p>Wind Speed: {weather.current.wind_speed} m/s</p>
              <p>Description: {weather.current.description}</p>
            </>
          ) : (
            <p className="text-red-500">No data available</p>
          )}
        </div>
      </div>
    </div>
  );
}
